import React from 'react';
import { useAppState } from 'shared/state';
import { useNotistack } from 'shared/hooks';
import EntityForm from 'modules/entity/forms';
import { FormHeader, Loader } from 'shared/components';
import LinkedRoleFormElements from './LinkedRoleFormElements';
import { LinkedClientRoleEntity } from 'shared/typings/client.types';
import { useTranslation } from 'react-i18next';

type Props = {
	onSuccess: () => void;
	entity: LinkedClientRoleEntity;
};

export function EditLinkedClientRole({ onSuccess, entity }: Props) {
	const { companyId } = useAppState();
	const { showNotification } = useNotistack();
	const { t } = useTranslation();

	if (!entity) {
		return null;
	}

	return (
		<div className="max-w-[446px]">
			<>
				<EntityForm.Main
					entity="linkedClientsRole"
					name="AllLinkedClientsRole"
					url={`/${companyId}/my/company/client/roles/${entity.id}/`}
					method="put"
					primaryKey="id"
					sendAsFormData={false}
					normalizeData={(data: any) => data}
					updateData
					fields={[
						{
							name: 'name',
							onSubmitKey: '',
							required: true,
							value: entity.name
						},
						{
							name: 'description',
							onSubmitKey: '',
							required: true,
							value: entity.description
						}
					]}
					onSuccess={(response, resetForm): any => {
						showNotification({ message: t('update_success') });
						onSuccess();
						resetForm();
					}}
					onError={(error, resetForm): any => {
						const errorText = error?.response?.data?.message || t('something_went_wrong');
						showNotification({ message: errorText, variant: 'error' });
					}}
				>
					{({ isSubmitting, values, resetForm }) => {
						if (process.env.NODE_ENV === 'development') {
							console.log('edit client role  values', values);
						}
						return (
							<>
								<FormHeader resetForm={resetForm} title={t('edit_client_group')} isEditing />
								<>
									<div className={'flex items-center'}>
										<LinkedRoleFormElements
											isSubmitting={isSubmitting}
											onSuccess={onSuccess}
											isEditing
										/>
										{isSubmitting && <Loader tiled withOverlay />}
									</div>
								</>
							</>
						);
					}}
				</EntityForm.Main>
			</>
		</div>
	);
}
