import { useAppState } from 'shared/state';
import { useDispatch } from 'react-redux';
import { useEntityActionPopup, useNotistack, usePageChangeAfterEntityDelete } from 'shared/hooks';
import { LinkedClientRoleEntity } from 'shared/typings/client.types';
import { formRequest } from 'modules/entity/actions';
import { useTranslation } from 'react-i18next';

export function useLinkedClientRoles() {
	const { companyId } = useAppState();
	const dispatch = useDispatch();
	const { showNotification } = useNotistack();
	const { t } = useTranslation();
	const {
		visible: deleteVisible,
		showVisible: showDeleteVisible,
		loading: deleteLoading,
		toggleLoading: toggleDeleteLoading,
		closePopup: closeDeletePopup
	} = useEntityActionPopup<LinkedClientRoleEntity>();

	const {
		visible: editVisible,
		showVisible: showEditVisible,
		closePopup: closeEditPopup
	} = useEntityActionPopup<LinkedClientRoleEntity>();

	const { forwardToPreviousPage } = usePageChangeAfterEntityDelete({
		pageKey: 'page',
		entity: 'linkedClientsRole',
		entityName: 'AllLinkedClientsRole'
	});

	const handleLinkedClientDelete = (linkedRole: LinkedClientRoleEntity) => {
		//show loader
		toggleDeleteLoading();
		dispatch(
			formRequest({
				entity: 'linkedClientsRole',
				name: `AllLinkedClientsRole`,
				method: 'delete',
				url: `/${companyId}/my/company/client/roles/${linkedRole.id}/`,
				deleteData: true,
				id: linkedRole.id,
				cb: {
					success: () => {
						forwardToPreviousPage();
						showNotification({ message: t('successfully_deleted'), variant: 'success' });
					},
					error: (err) => {
						showNotification({ message: t('something_went_wrong'), variant: 'error' });
					},
					finally: () => {
						toggleDeleteLoading();
						closeDeletePopup();
					}
				},
				params: {},
				primaryKey: 'id',
				values: {}
			})
		);
	};

	const deleteLinkedClientHandler = () => () => handleLinkedClientDelete(deleteVisible!);

	return {
		deleteLinkedClientHandler,
		deleteVisible,
		showDeleteVisible,
		deleteLoading,
		closeDeletePopup,
		showEditVisible,
		closeEditPopup,
		editVisible
	};
}
