import React from 'react';
import {
	ActionsButtons,
	ConfirmModal,
	ErrorBoundary,
	Loader,
	StyledTable,
	StyledTableCell,
	StyledTableHeadCell,
	StyledTableRow
} from 'shared/components';
import { useAppState } from 'shared/state';
import EntityContainer from 'modules/entity/containers';
import {
	Pagination,
	TableBody,
	TableContainer,
	TableHead,
	WhiteModal
} from 'shared/components/Elements';
import { usePagination } from 'shared/hooks';
import { useClientsManagementContext } from '../../context/ClientsManagementContext';
import { LinkedClientRoleEntity } from 'shared/typings/client.types';
import { useDebounce } from 'use-debounce';
import { useLinkedClientRoles } from '../../hooks/useLinkedClientRoles';
import { EditLinkedClientRole } from '../../components/LinkenClientRoles/EditLinkedClientRole';
import { useTranslation } from 'react-i18next';

function LinkedClientsRolesInner() {
	const { t } = useTranslation();
	const { companyId, isPermitted } = useAppState();
	const { page, handlePage } = usePagination();
	const { searchQuery: query } = useClientsManagementContext();
	const [searchQuery] = useDebounce<string>(query, 500);

	const {
		deleteLinkedClientHandler,
		deleteVisible,
		showDeleteVisible,
		deleteLoading,
		closeDeletePopup,
		showEditVisible,
		closeEditPopup,
		editVisible
	} = useLinkedClientRoles();

	return (
		<div>
			{companyId ? (
				<EntityContainer.All
					entity="linkedClientsRole"
					name="AllLinkedClientsRole"
					url={`/${companyId}/my/company/client/roles/`}
					params={{
						limit: 10,
						extra: {
							search: searchQuery
						},
						page
					}}
				>
					{({ items, isFetched, meta }) => {
						const linkedClientRoles = items as LinkedClientRoleEntity[];
						return (
							<div>
								<TableContainer classes={{ root: 'relative ' }}>
									<StyledTable
										aria-label="simple table"
										classes={{ root: 'table-fixed dark-mode-styled-table' }}
									>
										<TableHead>
											<StyledTableRow>
												<StyledTableHeadCell style={{ width: '60%' }}>
													{t('role_name')}
												</StyledTableHeadCell>
												<StyledTableHeadCell style={{ width: '20%' }}>
													{t('description')}
												</StyledTableHeadCell>
												<StyledTableHeadCell style={{ width: '20%' }}>
													<div className="flex items-center justify-center">{t('actions')}</div>
												</StyledTableHeadCell>
											</StyledTableRow>
										</TableHead>
										<TableBody>
											{linkedClientRoles.map((item) => {
												const { description, name, id } = item;
												return (
													<StyledTableRow key={id}>
														<StyledTableCell>{name}</StyledTableCell>
														<StyledTableCell>{description}</StyledTableCell>
														<StyledTableCell>
															<div className="flex items-center justify-center">
																<ActionsButtons.DeleteButton
																	isDisabled={!isPermitted}
																	clickHandler={() => showDeleteVisible(item)}
																/>
																<ActionsButtons.EditButton
																	isDisabled={!isPermitted}
																	clickHandler={() => showEditVisible(item)}
																/>
															</div>
														</StyledTableCell>
													</StyledTableRow>
												);
											})}
										</TableBody>
									</StyledTable>
									{!isFetched && <Loader tiled withOverlay />}
								</TableContainer>
								<ConfirmModal
									featureHandler={deleteLinkedClientHandler()}
									isOpen={Boolean(deleteVisible)}
									toggleHandler={closeDeletePopup}
									title={t('delete_linked_client_confirmation', { name: deleteVisible?.name })}
									hasLoader
									isLoading={deleteLoading}
								/>
								<WhiteModal handleModalOpen={closeEditPopup} open={Boolean(editVisible)}>
									<EditLinkedClientRole onSuccess={closeEditPopup} entity={editVisible!} />
								</WhiteModal>
								<div className="news__pagination my-4">
									{items.length ? (
										<Pagination
											count={meta?.last_page}
											page={page ? +page : 1}
											defaultPage={6}
											siblingCount={1}
											color={'primary'}
											onChange={handlePage}
										/>
									) : null}
								</div>
							</div>
						);
					}}
				</EntityContainer.All>
			) : (
				<div className="flex justify-center">
					<Loader />
				</div>
			)}
		</div>
	);
}

export default function LinkedClientsRoles() {
	return (
		<ErrorBoundary>
			<LinkedClientsRolesInner />
		</ErrorBoundary>
	);
}
